import React from "react";

const TaskInput = ({
  handleEditInputChange,
  setIsEditing,
  handleEditFormSubmit,
  isEditing,
  currentTodo,
  handleInputChange,
  todo,
  handleFormSubmit,
}) => {
  return (
    <section className="intro">
      {isEditing ? (
        <form className="form">
          <input
            name="editTodo"
            type="text"
            placeholder="Edit todo"
            value={currentTodo.text}
            className="new-input"
            onChange={handleEditInputChange}
          />
          <div>
            <input
              onClick={handleEditFormSubmit}
              className="Edit-submit"
              type="submit"
              value="Update task"
              id="new-task_submit"
            />

            <button className="delete" onClick={() => setIsEditing(false)}>
              Cancel
            </button>
          </div>
        </form>
      ) : (
        <form className="form">
          <input
            onChange={handleInputChange}
            value={todo}
            className="new-input"
            type="text"
            placeholder="What do you want to do?"
          />
          <input
            onClick={handleFormSubmit}
            className="submit"
            type="submit"
            value="Add task"
            id="new-task_submit"
          />
        </form>
      )}
    </section>
  );
};

export default TaskInput;



