import React from "react";
import { useEffect, useState } from "react";
import Navbar from "./components/nav";
import TaskInput from "./components/taskInput";
import TodoList from "./components/Todolist";
import "./App.css";

export default function App() {
  const [todos, setTodos] = useState(() => {
    const savedTodos = localStorage.getItem("todos");
    if (savedTodos) {
      return JSON.parse(savedTodos);
    } else {
      return [];
    }
  });
  const [todo, setTodo] = useState("");

  const [isEditing, setIsEditing] = useState(false);

  const [currentTodo, setCurrentTodo] = useState({});

  useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos]);

  const handleInputChange = (e) => {
    setTodo(e.target.value);
  };

  const handleEditInputChange = (e) => {
    setCurrentTodo({ ...currentTodo, text: e.target.value });
  };

  const handleFormSubmit = (e) => {
    e.preventDefault();

    if (todo !== "") {
      setTodos([
        ...todos,
        {
          id: todos.length + 1,
          text: todo.trim(),
        },
      ]);
    }

    setTodo("");
  };
  const handleUpdateTodo = (id, updatedTodo) => {
    const updatedItem = todos.map((todo) => {
      return todo.id === id ? updatedTodo : todo;
    });
    setIsEditing(false);

    setTodos(updatedItem);
  };

  const handleEditFormSubmit = (e) => {
    e.preventDefault();

    handleUpdateTodo(currentTodo.id, currentTodo);
  };

  const handleDeleteClick = (id) => {
    const removeItem = todos.filter((todo) => {
      return todo.id !== id;
    });
    setTodos(removeItem);
  };

  const handleEditClick = (todo) => {
    setIsEditing(true);

    setCurrentTodo({ ...todo });
  };

  const checkTasks = (variable) => {
    var num = todos.length;
    return num > 0 ? (
      <h2 className={variable}>
        {" "}
        <span>{num}</span> {num === 1 ? "task to be done" : "tasks to be done"}
      </h2>
    ) : (
      <h2 className={variable}> There are no tasks yet please add a task</h2>
    );
  };

  return (
    <React.Fragment>
      <div className="container">
        <Navbar checkTasks={checkTasks} />
        <div className="main">
          <TaskInput
            handleEditInputChange={handleEditInputChange}
            setIsEditing={setIsEditing}
            handleEditFormSubmit={handleEditFormSubmit}
            isEditing={isEditing}
            currentTodo={currentTodo}
            handleInputChange={handleInputChange}
            todo={todo}
            handleFormSubmit={handleFormSubmit}
          />
          <TodoList
            handleEditClick={handleEditClick}
            checkTasks={checkTasks}
            todos={todos}
            handleDeleteClick={handleDeleteClick}
          />
        </div>
      </div>
    </React.Fragment>
  );
}



