import React from "react";

const TodoList = ({
  handleEditClick,
  checkTasks,
  todos,
  handleDeleteClick,
}) => {
  return (
    <section className="task-list_container">
      {checkTasks("tasks-sub")}
      <ul className="task-list">
        {todos.map((todo) => (
          <li key={todo.id}>
            <input className="task" value={todo.text} readOnly type="text" />

            <div>
              <button className="edit" onClick={() => handleEditClick(todo)}>
                Edit
              </button>
              <button
                className="delete"
                onClick={() => handleDeleteClick(todo.id)}
              >
                Delete
              </button>
            </div>
          </li>
        ))}
      </ul>
    </section>
  );
};

export default TodoList;



